<template>
  <div :class="{ 'border': showBorder, winner: isWinner }" class="match-result-sheet-row">
    <span class="name"><span class="prefix">{{prefix}}</span>{{player.fullname}}<sup v-if="pointsAssigned">{{pointsAssigned > 0 ? '+' : ''}}{{pointsAssigned}}</sup></span>
    <span v-for="(score, i) in resultList" :key="i" :class="{ odd: i % 2 !== 0, even: i % 2 === 0 }" class="score">{{score}}</span>
    <span class="result">{{result}}</span>
  </div>
</template>
<script>
export default {
  name: 'MatchResultSheetRow',
  props: {
    player: {
      type: Object,
      default: () => ({}),
    },
    resultList: {
      type: Array,
      default: () => [],
    },
    result: {
      type: Number,
      default: 0,
    },
    pointsAssigned: {
      type: Number,
      default: 0,
    },
    isWinner: {
      type: Boolean,
      default: undefined,
    },
    isCurrentServer: {
      type: Boolean,
      default: false,
    },
    showBorder: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    prefix() {
      if (this.isWinner === true) {
        return ''
      }

      if (this.isWinner === undefined && this.isCurrentServer) {
        return ''
      }
      return ''
    },
  },
}
</script>
<style scoped>
.match-result-sheet-row {
  display: flex;
  flex-direction: row;
  height: 36px;
  position: relative;
}
.name {
  position: relative;
  display: flex;
  align-items: center;
  margin-left: 32px;
  flex: 1;
  background-color: #18b493;
  color: white;
}
.prefix {
  position: absolute;
  left: -20px;
}
.score {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  color: #5a5a5a;
}
.score.odd {
  background-color: #f5f5f5;
}
.score.even {
  background-color: white;
}
.result {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  background-color: #18b493;
  color: white;
}
.winner .name {
  color: #ffec3d;
  font-weight: bold;
}
.winner .result {
  color: #ffec3d;
  font-weight: bold;
}
.border .name {
  border-bottom: 1px solid white;
}
.border .score {
  border-bottom: 1px solid #18b493;
}
.border .result {
  border-bottom: 1px solid white;
}
</style>
