<template>
  <f7-page :page-content="false">
    <f7-toolbar tabbar no-hairline labels class="color-theme-yellow bg-color-white">
      <f7-link tab-link href="/" route-tab-id="home">
        <f7-icon ion="ios-home" size="24"/>
        <span class="tabbar-label">Home</span>
      </f7-link>
      <f7-link tab-link href="/ranking" route-tab-id="ranking">
        <f7-icon ion="ios-trophy" size="24"/>
        <span class="tabbar-label">Ranking</span>
      </f7-link>
      <f7-link id="create-match" @click="createMatch">
        <f7-icon ion="ios-add" size="38"/>
        <div class="icon-placeholder">placeholder</div>
        <span class="tabbar-label">PLAY</span>
      </f7-link>
      <f7-link tab-link href="/matches" route-tab-id="matches">
        <f7-icon ion="ios-list-box" size="24"/>
        <span class="tabbar-label">Matches</span>
      </f7-link>
      <f7-link tab-link href="/profile" route-tab-id="profile">
        <f7-icon ion="ios-person" size="24"/>
        <span class="tabbar-label">Profile</span>
      </f7-link>
    </f7-toolbar>
    <f7-tabs routable>
      <f7-tab id="home" class="page-content"/>
      <f7-tab id="ranking" class="page-content"/>
      <f7-tab id="matches" class="page-content"/>
      <f7-tab id="profile" class="page-content"/>
    </f7-tabs>
  </f7-page>
</template>
<script>
export default {
  name: 'MainTabs',
  methods: {
    createMatch() {
      const user = this.$root.user
      if (!user) {
        return this.$f7.toast.show({
          text: 'Please sign in first.',
          position: 'center',
          closeTimeout: 3000,
        })
      }

      if (user.isInMatch) {
        return this.$f7.toast.show({
          text: 'You are in a ongoing match.',
          position: 'center',
          closeTimeout: 3000,
        })
      }

      if (!user.verified) {
        return this.$f7.toast.show({
          text: 'Please become a verified member first.',
          position: 'center',
          closeTimeout: 3000,
        })
      }

      this.$f7router.navigate('/create-match')
    },
  },
}
</script>
