<template>
  <div class="ranking">
    <f7-navbar title="Ranking" no-hairline class="bg-color-white"/>
    <loader v-if="!isInitialLoadingFinished"/>
    <ul class="ranking-list">
      <ranking-cell v-for="(player, idx) in players" :key="player.id" :rank="idx+1" :player="player"/>
    </ul>
  </div>
</template>
<script>
import Loader from 'components/Loader'
import RankingCell from 'components/RankingCell'

export default {
  name: 'Ranking',
  components: {
    Loader,
    RankingCell,
  },
  data() {
    return {
      isInitialLoadingFinished: false,
      players: [],
    }
  },
  created() {
    this.$db
      .ref('users')
      .orderByChild('verified')
      .equalTo(true)
      .on('value', (snapshot) => {
        const data = snapshot.val() || {}
        /* eslint-disable-next-line */
      const players = Object.values(data).map((player, i) => ({ ...player, id: Object.keys(data)[i] }))
        players.sort((a, b) => b.points - a.points)
        this.players = players
        this.isInitialLoadingFinished = true
      })
  },
}
</script>
<style scoped>
.ranking-list {
  margin: 16px;
  padding: 0;
}
</style>
