<template>
  <div class="matches">
    <f7-navbar title="Matches" no-hairline class="bg-color-white"/>
    <loader v-if="!isInitialLoadingFinished"/>
    <template v-else>
      <h3 v-show="unfinishedMatches.length">Live Matches</h3>
      <ul class="match-list">
        <li v-for="match in unfinishedMatches" :key="match.id" @click="viewMatch(match)">
          <span class="date">{{distanceInWordsToNow(match.createdAt)}}</span>
          <match-result-sheet :match="match"/>
        </li>
      </ul>
      <h3 v-show="finishedMatches.length">Finished Matches</h3>
      <ul class="match-list">
        <li v-for="match in finishedMatches" :key="match.id" @click="viewMatch(match)">
          <span class="date">{{format(match.finishedAt)}}</span>
          <match-result-sheet :match="match"/>
        </li>
      </ul>
    </template>
  </div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
import format from 'date-fns/format'
import distanceInWordsToNow from 'date-fns/distance_in_words_to_now'
import Loader from 'components/Loader'
import MatchResultSheet from 'components/MatchResultSheet.vue'

const MATCH_PER_PAGE = 30

export default {
  name: 'Matches',
  components: {
    Loader,
    MatchResultSheet,
  },
  data() {
    return {
      isInitialLoadingFinished: false,
      page: 0,
      finishedMatchIds: [],
      finishedMatches: [],
      unfinishedMatches: [],
    }
  },
  computed: {
    user() {
      return this.$root.user
    },
    totalPage() {
      return Math.ceil(this.finishedMatchIds.length / MATCH_PER_PAGE)
    },
    canLoadMore() {
      return this.page < this.totalPage
    },
  },
  created() {
    this.$db
      .ref('matches')
      .orderByChild('finishedAt')
      .equalTo(null)
      .once('value')
      .then((snapshot) => {
        const data = snapshot.val() || {}
        const unfinishedMatchIds = Object.keys(data)

        this.unfinishedMatches = unfinishedMatchIds.map((id) => ({ ...data[id], id }))
        this.unfinishedMatches.forEach((match) => {
          this.loadUserInfo(match)
        })

        axios
          .get('https://uwtta-web-app.firebaseio.com/matches.json?shallow=true')
          .then(({ data }) => {
            this.finishedMatchIds = Object.keys(data).filter(
              (id) => !unfinishedMatchIds.includes(id),
            )
            this.isInitialLoadingFinished = true
            this.loadMore()
          })
      })
    this.$db
      .ref('matches')
      .orderByChild('createdAt')
      .startAt(Date.now())
      .on('child_added', (snapshot) => {
        this.unfinishedMatches.unshift({ ...snapshot.val(), id: snapshot.key })
        this.loadUserInfo(this.unfinishedMatches[0])
      })
    this.$db.ref('matches').on('child_removed', (snapshot) => {
      const matchIndex = this.unfinishedMatches.findIndex((match) => match.id === snapshot.key)
      if (matchIndex !== -1) {
        Vue.delete(this.unfinishedMatches, matchIndex)
      }
    })
    this.$db.ref('matches').on('child_changed', (snapshot) => {
      const newVal = snapshot.val()
      newVal.id = snapshot.key
      let matchIndex
      matchIndex = this.unfinishedMatches.findIndex((match) => newVal.id === match.id)
      if (matchIndex !== -1) {
        if (newVal.finished) {
          Vue.delete(this.unfinishedMatches, matchIndex)
          return this.finishedMatches.push(newVal)
        }
        return this.updateMatchChange(this.unfinishedMatches[matchIndex], newVal)
      }
      matchIndex = this.finishedMatches.findIndex((match) => newVal.id === match.id)
      if (matchIndex !== -1) {
        return this.updateMatchChange(this.finishedMatches[matchIndex], newVal)
      }
    })
  },
  methods: {
    format(timestamp) {
      return format(new Date(timestamp), 'YYYY-MM-DD HH:mm')
    },
    distanceInWordsToNow(timestamp) {
      return distanceInWordsToNow(new Date(timestamp))
    },
    loadMore() {
      if (!this.canLoadMore) return

      Promise.all(
        this.finishedMatchIds
          .slice(this.page * MATCH_PER_PAGE, (this.page + 1) * MATCH_PER_PAGE)
          .map((id) => this.$db.ref(`matches/${id}`).once('value')),
      ).then((snapshots) => {
        snapshots.forEach((snapshot) => {
          this.finishedMatches.unshift({ ...snapshot.val(), id: snapshot.key })
          this.loadUserInfo(this.finishedMatches[0])
        })
      })
    },
    updateMatchChange(match, newMatch) {
      delete newMatch.createdBy
      delete newMatch.judge
      delete newMatch.player1
      delete newMatch.player2

      Object.keys(newMatch).forEach((key) => {
        Vue.set(match, key, newMatch[key])
      })
    },
    loadUserInfo(match) {
      ;['createdBy', 'judge', 'player1', 'player2'].forEach((key) => {
        this.$db.ref(`users/${match[key]}`).on('value', (snapshot) => {
          Vue.set(match, key, { ...snapshot.val(), id: snapshot.key })
        })
      })
    },
    viewMatch(match) {
      if (
        typeof match.createdBy === 'string' ||
        typeof match.judge === 'string' ||
        typeof match.player1 === 'string' ||
        typeof match.player2 === 'string'
      )
        return

      const title = `${match.player1.emoji}${match.player1.nickname} vs. ${match.player2.nickname}${
        match.player2.emoji
      }`
      let text
      if (match.finished) {
        text = 'Wanna view details of this match?'
      } else {
        text = [match.createdBy.id, match.judge.id].includes(this.user && this.user.uid)
          ? 'Judge, we are waiting for you! Ready to continue?'
          : 'Wanna watch this live match?'
      }
      this.$f7.dialog.confirm(text, title, () => {
        this.$f7router.navigate(`/scoreboard/${match.id}`)
      })
    },
  },
}
</script>
<style scoped>
.match-list {
  margin: 16px;
  padding: 0;
}
li {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin-bottom: 12px;
}
.date {
  margin: 4px 0;
  text-align: left;
  color: #666;
  font-size: 0.85em;
}
</style>
