import Vue from 'vue'
import Framework7 from 'framework7/dist/framework7.esm.bundle.js'
import Framework7Vue from 'framework7-vue/dist/framework7-vue.esm.bundle.js'
import firebase from 'firebase'
import routes from './routes.js'
import App from './App.vue'
import './registerServiceWorker'

/* eslint-disable-next-line no-unused-vars */
import Framework7Styles from 'framework7/dist/css/framework7.css'
/* eslint-disable-next-line no-unused-vars */
import IonIcons from 'ionicons/dist/css/ionicons.css'
/* eslint-disable-next-line no-unused-vars */
import MaterialIcons from 'material-design-icons/iconfont/material-icons.css'
/* eslint-disable-next-line no-unused-vars */
import Framework7Icons from 'framework7-icons/css/framework7-icons.css'
/* eslint-disable-next-line no-unused-vars */
import AppStyle from './assets/css/style.css'

Vue.use(Framework7Vue, Framework7)

Vue.config.productionTip = false

firebase.initializeApp({
  apiKey: 'AIzaSyA6VDgDMCtwut3AnIoZa5D_MNZ-UmMgTtg',
  authDomain: 'uwtta-web-app.firebaseapp.com',
  databaseURL: 'https://uwtta-web-app.firebaseio.com',
  projectId: 'uwtta-web-app',
  storageBucket: 'uwtta-web-app.appspot.com',
  messagingSenderId: '861274003236',
})

Vue.prototype.$db = firebase.database()
Vue.prototype.$auth = firebase.auth()
Vue.prototype.$fns = firebase.functions()

new Vue({
  data() {
    return {
      user: null,
      userRef: '',
    }
  },
  created() {
    this.$auth.onAuthStateChanged((user) => {
      if (user) {
        this.user = { ...user }
        this.userRef = `users/${user.uid}`
        this.$db.ref(this.userRef).on('value', this.onUserProfileChange)
      } else {
        this.user = null
        if (this.userRef) {
          this.$db.ref(this.userRef).off('value', this.onUserProfileChange)
        }
      }
    })
  },
  methods: {
    onUserProfileChange(snapshot) {
      const profile = snapshot.val()
      if (!this.user.fullname) {
        const notif = this.$f7.notification.create({
          icon: '',
          title: 'Table Denis Club',
          titleRightText: 'now',
          subtitle: 'Welcome back!',
          text: `${profile.fullname}, how are you?`,
          closeTimeout: 3000,
        })
        notif.open()
      }
      this.user = {
        ...this.user,
        ...profile,
      }
    },
  },
  framework7: {
    root: '#app',
    id: 'me.chengkang.tdc',
    name: 'Table Denis Club - We are professionals!',
    theme: 'ios',
    routes,
  },
  render: (h) => h(App),
}).$mount('#app')
