<template>
  <div class="match-result-sheet">
    <MatchResultSheetRow
      v-for="i in [0, 1]"
      :key="i"
      :player="typeof match[`player${i+1}`] === 'string' ? {} : match[`player${i+1}`]"
      :result-list="resultLists[i]"
      :result="result[i]"
      :points-assigned="match.pointsAssigned && match.pointsAssigned[i]"
      :show-border="i === 0"
      :is-winner="winner === undefined ? undefined : winner === i"
      :is-current-server="currentServer === i"
    />
  </div>
</template>
<script>
import MatchResultSheetRow from 'components/MatchResultSheetRow.vue'

export default {
  name: 'MatchResultSheet',
  components: {
    MatchResultSheetRow,
  },
  props: {
    match: {
      type: Object,
      default: () => ({}),
    },
    currentServer: {
      type: Number,
      default: undefined,
    },
  },
  computed: {
    resultLists() {
      const temp = [[], []]
      this.match.resultTable.map(([one, two]) => {
        temp[0].push(one)
        temp[1].push(two)
      })
      return temp
    },
    result() {
      return this.match.resultTable.reduce(
        (acc, cur) => {
          if (cur[0] - cur[1] >= 2 && cur[0] >= this.match.pointsPerSet) {
            acc[0] += 1
          }
          if (cur[1] - cur[0] >= 2 && cur[1] >= this.match.pointsPerSet) {
            acc[1] += 1
          }
          return acc
        },
        [0, 0],
      )
    },
    isMatchFinished() {
      const winPoint = Math.ceil(this.match.numberOfSets / 2)
      return this.result[0] === winPoint || this.result[1] === winPoint
    },
    winner() {
      return this.isMatchFinished ? (this.result[0] > this.result[1] ? 0 : 1) : undefined
    },
  },
}
</script>
<style>
.match-result-sheet {
  border-radius: 3px;
  overflow: hidden;
  background-color: #18b493;
}
</style>
