<template>
  <div class="chat">
    <f7-messagebar ref="messagebar" :disabled="!user" :placeholder="user ? 'Chat now!' : 'Sign in to chat!'" @submit="onSubmit">
      <f7-icon slot="send-link" ion="ios-paper-plane" size="22" style="color: #17b492"/>
    </f7-messagebar>
    <f7-messages>
      <f7-messages-title v-if="messages.length === 0">Send the first message!</f7-messages-title>
      <f7-message
        v-for="(message, i) in messages"
        :key="message.id"
        :type="message.type"
        :text="message.text"
        :name="message.name"
        :avatar="message.avatar"
        :first="isFirstMessage(message, i)"
        :last="isLastMessage(message, i)"
        :tail="isTailMessage(message, i)"
      />
    </f7-messages>
  </div>
</template>
<script>
import Vue from 'vue'

export default {
  name: 'Chat',
  props: {
    matchId: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      messages: [],
    }
  },
  computed: {
    user() {
      return this.$root.user
    },
  },
  created() {
    this.$db.ref(`chats/${this.matchId}`).on('child_added', (snapshot) => {
      const message = snapshot.val()
      this.messages.unshift({
        id: snapshot.key,
        text: message.content,
        type: message.sentBy === (this.user && this.user.uid) ? 'sent' : 'received',
        sentBy: message.sentBy,
        name: 'Someone',
      })
      this.loadUserInfo(this.messages[0])
    })
  },
  methods: {
    loadUserInfo(message) {
      this.$db.ref(`users/${message.sentBy}`).on('value', (snapshot) => {
        const sender = snapshot.val()
        if (!sender) return
        Vue.set(message, 'name', sender.fullname)
        Vue.set(message, 'avatar', sender.photoURL)
      })
    },
    isFirstMessage(message, index) {
      const previousMessage = this.messages[index - 1]
      if (
        !previousMessage ||
        previousMessage.type !== message.type ||
        previousMessage.name !== message.name
      )
        return true
      return false
    },
    isLastMessage(message, index) {
      const nextMessage = this.messages[index + 1]
      if (!nextMessage || nextMessage.type !== message.type || nextMessage.name !== message.name)
        return true
      return false
    },
    isTailMessage(message, index) {
      const nextMessage = this.messages[index + 1]
      if (!nextMessage || nextMessage.type !== message.type || nextMessage.name !== message.name)
        return true
      return false
    },
    onSubmit(value) {
      if (!value || !this.user) return

      this.$db
        .ref(`chats/${this.matchId}`)
        .push({
          content: value,
          sentBy: this.user.uid,
          createdAt: new Date().getTime(),
        })
        .then(() => {
          this.$refs.messagebar.f7Messagebar.clear()
        })
    },
  },
}
</script>
<style>
.chat {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}
</style>
