<template>
  <f7-page>
    <f7-page-content class="container">
      <div class="close-button" @click="$f7router.back()"><f7-icon ion="ios-close" size="42"/></div>
      <div class="header">Ping Pong?</div>
      <note>
        <small>Read <a href="#rules">the rules</a> at the bottom first!</small>
      </note>
      <div class="legend text-align-left">New Match</div>
      <div class="field">
        <label class="uk-label" for="judge">Judge</label>
        <select id="judge" v-model="judge" class="uk-select">
          <option :value="{}"/>
          <option v-for="player in judgeList" :key="player.id" :value="player">{{player.fullname}}</option>
        </select>
      </div>
      <div class="field">
        <label class="uk-label" for="player1">Player 1</label>
        <select id="player1" v-model="player1" class="uk-select">
          <option :value="{}"/>
          <option v-for="player in player1List" :key="player.id" :value="player">{{player.fullname}}</option>
        </select>
      </div>
      <div class="field">
        <label class="uk-label" for="player2">Player 2</label>
        <select id="player2" v-model="player2" class="uk-select">
          <option :value="{}"/>
          <option v-for="player in player2List" :key="player.id" :value="player">{{player.fullname}}</option>
        </select>
      </div>
      <div class="field">
        <label class="uk-label" for="numberOfSets">Number of sets</label>
        <div class="input-group">
          <label><input v-model="numberOfSets" value="1" class="uk-radio" type="radio" name="numberOfSets">1</label>
          <label><input v-model="numberOfSets" value="3" class="uk-radio" type="radio" name="numberOfSets">3</label>
          <label><input v-model="numberOfSets" value="5" class="uk-radio" type="radio" name="numberOfSets">5</label>
        </div>
      </div>
      <div class="field">
        <label class="uk-label" for="pointsPerSet">Points per set</label>
        <div class="input-group">
          <label><input v-model="pointsPerSet" value="5" class="uk-radio" type="radio" name="pointsPerSet">5</label>
          <label><input v-model="pointsPerSet" value="11" class="uk-radio" type="radio" name="pointsPerSet">11</label>
          <label><input v-model="pointsPerSet" value="21" class="uk-radio" type="radio" name="pointsPerSet">21</label>
        </div>
      </div>
      <div v-if="error" class="error-message">{{error}}</div>
      <div class="actions">
        <span class="uk-button" @click="createMatch">Fight!</span>
      </div>
      <rules/>
    </f7-page-content>
  </f7-page>
</template>
<script>
import Note from 'components/Note'
import Rules from 'components/Rules'

export default {
  name: 'Scoreboard',
  components: {
    Note,
    Rules,
  },
  data() {
    return {
      players: [],
      judge: undefined,
      player1: undefined,
      player2: undefined,
      numberOfSets: 3,
      pointsPerSet: 11,
      error: undefined,
    }
  },
  computed: {
    user() {
      return this.$root.$data.user
    },
    judgeList() {
      return this.players.filter((player) => player !== this.player1 && player !== this.player2)
    },
    player1List() {
      return this.players.filter((player) => player !== this.judge && player !== this.player2)
    },
    player2List() {
      return this.players.filter((player) => player !== this.player1 && player !== this.judge)
    },
  },
  created() {
    this.$f7.dialog.preloader('Loading...')
    this.$db
      .ref('users')
      .orderByChild('verified')
      .equalTo(true)
      .once('value')
      .then((snapshot) => {
        const players = snapshot.val()
        this.players = Object.keys(players).map((uid) => ({ ...players[uid], id: uid }))
        this.judge = this.players.find((player) => player.id === this.user.uid)
        this.$f7.dialog.close()
      })
  },
  methods: {
    createMatch() {
      if (!this.players.includes(this.judge)) {
        return (this.error = 'Please select a judge.')
      }
      if (!this.players.includes(this.player1)) {
        return (this.error = 'Please select player1.')
      }
      if (!this.players.includes(this.player2)) {
        return (this.error = 'Please select a player2.')
      }
      this.$f7.dialog.preloader('Creating match...')
      this.$fns
        .httpsCallable('createMatch')({
          judge: this.judge.id,
          player1: this.player1.id,
          player2: this.player2.id,
          numberOfSets: this.numberOfSets,
          pointsPerSet: this.pointsPerSet,
        })
        .then(({ data: { matchId, judge, player1, player2 } }) => {
          this.$f7.dialog.close()
          const notif = this.$f7.notification.create({
            icon: '',
            title: 'Table Denis Club',
            titleRightText: 'now',
            subtitle: 'Match Created!',
            text: `${player1.emoji}${player1.nickname} vs. ${player2.nickname}${player2.emoji}, judged by ${judge.nickname}. Fight!`,
            closeTimeout: 3000,
            closeOnClick: true,
          })
          notif.open()
          this.$f7router.navigate(`/scoreboard/${matchId}`, { reloadCurrent: true })
        })
        .catch((error) => {
          this.error = error.message
          this.$f7.dialog.close()
        })
    },
  },
}
</script>

