<template>
  <div :class="{ gold: rank === 1, silver: rank === 2, bronze: rank === 3, top3: rank < 4 }" class="ranking-cell">
    <div v-if="player.isInMatch" class="is-playing blink"/>
    <span class="rank">{{ bakedRank }}</span>
    <img :src="player.photoURL" class="photo">
    <div class="info">
      <span v-if="rank < 4 && player.title" class="title shimmer">{{player.title}}</span>
      <span class="name">{{player.fullname}}</span>
      <span v-if="rank < 4 && player.words" class="words">- {{player.words}}</span>
    </div>
    <span class="points">{{player.points}}</span>
  </div>
</template>
<script>
export default {
  name: 'RankingCell',
  props: {
    rank: {
      type: Number,
      default: 0,
    },
    player: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    bakedRank() {
      if (this.rank === 1) {
        return ''
      }
      if (this.rank === 2) {
        return ''
      }
      if (this.rank === 3) {
        return ''
      }
      return this.rank
    },
  },
}
</script>
