<template>
  <div class="profile">
    <f7-navbar title="Profile" no-hairline class="bg-color-white"/>
    <template v-if="user">
      <div class="main-content">
        <div class="info">
          <img :src="user.photoURL" class="photo">
          <span class="name">{{user.fullname}} {{user.emoji}}</span>
          <span v-if="user.bio" class="bio">- {{user.bio}}</span>
        </div>
        <div class="status">
          <status-cell type="info" title="POINTS" value="122"/>
          <status-cell type="success" title="WON" value="18"/>
          <status-cell type="danger" title="LOST" value="8"/>
        </div>
      </div>
      <div class="actions">
        <f7-button color="white" text-color="black" fill @click="signOut">Sign Out</f7-button>
      </div>
    </template>
    <template v-else>
      <h1 class="text-align-left">Table Denis Club</h1>
      <f7-list inset class="text-align-left">
        <f7-list-item>
          <f7-label>E-mail</f7-label>
          <f7-input :value="form.email" type="email" placeholder="Your e-mail" required validate error-message="Please input a correct email" clear-button @input="form.email = $event.target.value"/>
        </f7-list-item>
        <f7-list-item>
          <f7-label>Password</f7-label>
          <f7-input :value="form.password" type="password" placeholder="Your password" required pattern=".{8,}" error-message="Must be at least 8 digits" validate clear-button @input="form.password = $event.target.value"/>
        </f7-list-item>
      </f7-list>
      <div v-if="form.error" class="error-message">{{form.error}}</div>
      <div class="actions">
        <f7-button :disabled="!validated" color="white" text-color="black" fill @click="signIn">Sign In</f7-button>
        <span class="separator">or</span>
        <f7-link href="/sign-up" color="black">Sign Up</f7-link>
      </div>
    </template>
  </div>
</template>
<script>
import StatusCell from 'components/StatusCell'

export default {
  name: 'Profile',
  components: {
    StatusCell,
  },
  data() {
    return {
      form: {
        email: '',
        password: '',
        error: '',
      },
    }
  },
  computed: {
    validated() {
      /* eslint-disable-next-line */
      const emailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      const pwRegex = /.{8,}/
      return emailRegex.test(this.form.email) && pwRegex.test(this.form.password)
    },
    user() {
      return this.$root.$data.user
    },
  },
  methods: {
    signIn() {
      this.$f7.dialog.preloader('Signing in...')
      this.$auth
        .signInWithEmailAndPassword(this.form.email, this.form.password)
        .then(() => {
          this.$f7.dialog.close()
        })
        .catch((error) => {
          this.form = {
            email: '',
            password: '',
            error: error ? error.message : '',
          }
          this.$f7.dialog.close()
        })
    },
    signOut() {
      this.$auth.signOut().then(() => {
        // Sign-out successful.
      })
    },
  },
}
</script>

