<template>
  <f7-page>
    <f7-page-content class="container">
      <loader v-if="!isGameLoaded"/>
      <template v-else>
        <template v-if="match.firstServer === undefined">
          <div class="first-server-picker">
            <div v-for="(player, i) in [match.player1, match.player2]" :key="i" :class="{ p2: i === 1 }" class="player">
              <img :src="player.photoURL" class="photoURL" @click="selectedFirstServer = i">
              <span class="name">{{selectedFirstServer === i ? '' : ''}}{{player.fullname}}</span>
              <span class="points">{{match.isFinished ? player.points : match.pointsBeforeMatch[i]}}</span>
            </div>
            <span class="v">V</span>
            <span class="s">S</span>
            <span class="instruction">Please select first server by clicking on player photo:</span>
            <span v-show="selectedFirstServer !== undefined" class="uk-button set-first-server" @click="confirmFisrtServer">Confirm</span>
          </div>
        </template>
        <template v-else>
          <f7-swiper id="swiper" :init="isGameLoaded" class="swiper" navigation>
            <f7-swiper-slide v-for="(resultList, idx) in match.resultTable" :key="idx" class="scoreboard">
              <div class="scores">
                <div>
                  <span @click="win(order[0])">
                    {{resultList[order[0]]}}
                  </span>
                  <span class="decrease-button" @click="decrease(order[0])"/>
                </div>
                <span>:</span>
                <div>
                  <span @click="win(order[1])">
                    {{resultList[order[1]]}}
                  </span>
                  <span class="decrease-button" @click="decrease(order[1])"/>
                </div>
              </div>
            </f7-swiper-slide>
          </f7-swiper>
          <div class="players">
            <div class="player">
              <img :src="players[0].photoURL" class="photoURL">
              <div class="info">
                <span class="name">{{players[0].fullname}}</span>
                <span class="points">{{players[0].points}}</span>
              </div>
            </div>
            <div class="player p2">
              <div class="info">
                <span class="name">{{players[1].fullname}}</span>
                <span class="points">{{players[1].points}}</span>
              </div>
              <img :src="players[1].photoURL" class="photoURL">
            </div>
          </div>
          <match-result-sheet :match="match" :current-server="currentServer" :winner="winner"/>
          <f7-segmented class="menu">
            <f7-button color="white" fill icon-ion="ios-chatboxes" @click="leftPanelOpened = true"/>
            <f7-button color="white" fill icon-ion="ios-more" @click="rightPanelOpened = true"/>
          </f7-segmented>
          <div :class="{ active: leftPanelOpened }" class="left-panel">
            <chat/>
            <div :class="{ active: leftPanelOpened }" class="close-left-panel" @click="leftPanelOpened = false"/>
          </div>
          <div :class="{ active: rightPanelOpened }" class="right-panel">
            <template v-if="canMakeChange">
              <template v-if="!match.finished">
                <f7-button :disabled="!isMatchFinished" color="black" fill @click="save">Save</f7-button>
                <f7-button color="black" @click="cancel">Cancel</f7-button>
              </template>
              <template v-else>
                <span>Match finished at {{format(match.finishedAt)}}.</span>
              </template>
            </template>
            <template v-else>
              <template v-if="!match.finished">
                <span>Match has been going for {{distanceInWordsToNow(match.createdAt)}}.</span>
              </template>
              <template v-else>
                <span>Match finished at {{format(match.finishedAt)}}.</span>
              </template>
            </template>
            <div class="separator"/>
            <f7-button color="black" @click="leave">Leave</f7-button>
            <div :class="{ active: rightPanelOpened }" class="close-right-panel" @click="rightPanelOpened = false"/>
          </div>
        </template>
      </template>
    </f7-page-content>
  </f7-page>
</template>
<script>
import Vue from 'vue'
import format from 'date-fns/format'
import distanceInWordsToNow from 'date-fns/distance_in_words_to_now'
import Loader from 'components/Loader'
import MatchResultSheet from 'components/MatchResultSheet'
import Chat from 'components/Chat'

export default {
  name: 'Scoreboard',
  components: {
    Loader,
    MatchResultSheet,
    Chat,
  },
  data() {
    return {
      match: null,
      selectedFirstServer: undefined,
      currentDisplayingSet: 0,
      isSwiperListenerSet: false,
      leftPanelOpened: false,
      rightPanelOpened: false,
    }
  },
  computed: {
    isGameLoaded() {
      return (
        this.match &&
        typeof this.match.createdBy !== 'string' &&
        typeof this.match.judge !== 'string' &&
        typeof this.match.player1 !== 'string' &&
        typeof this.match.player2 !== 'string'
      )
    },
    canMakeChange() {
      return (
        this.isGameLoaded &&
        [this.match.judge.id, this.match.createdBy.id].includes(
          this.$root.user && this.$root.user.uid,
        )
      )
    },
    firstServer() {
      return (this.match.firstServer + this.currentDisplayingSet % 2) % 2
    },
    currentServer() {
      let firstServerOfCurrentDisplayingSet
      if (this.currentDisplayingSet === 0) {
        firstServerOfCurrentDisplayingSet = this.firstServer
      } else {
        const prevResult = this.match.resultTable[this.currentDisplayingSet - 1]
        firstServerOfCurrentDisplayingSet = prevResult[0] > prevResult[1] ? 0 : 1
      }
      const totalPoints = this.match.resultTable[this.currentDisplayingSet].reduce(
        (a, c) => a + c,
        0,
      )
      const server = Math.round((totalPoints + 1) / 2) % 2 ? 0 : 1
      return firstServerOfCurrentDisplayingSet === 0 ? server : server ? 0 : 1
    },
    currentReceiver() {
      return this.currentServer == 0 ? 1 : 0
    },
    order() {
      const left = this.currentDisplayingSet % 2
      const right = left === 0 ? 1 : 0
      return [left, right]
    },
    players() {
      return [this.match[`player${this.order[0] + 1}`], this.match[`player${this.order[1] + 1}`]]
    },
    result() {
      return this.match.resultTable.reduce(
        (acc, cur) => {
          if (cur[0] - cur[1] >= 2 && cur[0] >= this.match.pointsPerSet) {
            acc[0] += 1
          }
          if (cur[1] - cur[0] >= 2 && cur[1] >= this.match.pointsPerSet) {
            acc[1] += 1
          }
          return acc
        },
        [0, 0],
      )
    },
    currentPlayingSet() {
      // TODO: judge can only modify result of current playing set of a unsaved match
      return Math.max(0, this.result[0] + this.result[1] - 1)
    },
    isMatchSaved() {
      return this.match.finished
    },
    isMatchFinished() {
      if (this.isMatchSaved) return true

      const winPoint = Math.ceil(this.match.numberOfSets / 2)
      return this.result[0] === winPoint || this.result[1] === winPoint
    },
    winner() {
      return this.isMatchFinished ? (this.result[0] > this.result[1] ? 0 : 1) : undefined
    },
  },
  updated() {
    if (
      this.isGameLoaded === true &&
      this.match.firstServer !== undefined &&
      !this.isSwiperListenerSet
    ) {
      this.isSwiperListenerSet = true
      this.$nextTick(() => {
        const swiper = this.$f7.swiper.get('#swiper')
        swiper.on('slideChange', () => {
          this.currentDisplayingSet = swiper.activeIndex
        })
      })
    }
  },
  created() {
    const { id: matchId } = this.$f7route.params

    this.$db.ref(`matches/${matchId}`).on('value', (snapshot) => {
      const data = snapshot.val()

      if (!data) {
        this.$f7router.navigate('/matches', { reloadCurrent: true })
        this.$f7.toast.show({
          text: 'Match not found.',
          closeTimeout: 3000,
        })
        return
      }

      if (!this.isGameLoaded) {
        this.match = { ...data, id: matchId }
        this.loadUserInfo()
        return
      }

      delete data.createdBy
      delete data.judge
      delete data.player1
      delete data.player2
      this.match = { ...this.match, ...data }
    })
  },
  methods: {
    format(timestamp) {
      return format(new Date(timestamp), 'YYYY-MM-DD HH:mm')
    },
    distanceInWordsToNow(timestamp) {
      return distanceInWordsToNow(new Date(timestamp))
    },
    loadUserInfo() {
      ;['createdBy', 'judge', 'player1', 'player2'].forEach((key) => {
        this.$db.ref(`users/${this.match[key]}`).on('value', (snapshot) => {
          Vue.set(this.match, key, { ...snapshot.val(), id: snapshot.key })
        })
      })
    },
    confirmFisrtServer() {
      if (!this.canMakeChange) return

      this.$f7.dialog.preloader('Setting first server...')
      // TODO: add security rule and validation rule
      this.$db
        .ref(`matches/${this.match.id}/firstServer`)
        .set(this.selectedFirstServer)
        .then(() => {
          this.$f7.dialog.close()
        })
        .catch((error) => {
          this.$f7.toast.show({
            text: error.message,
            closeTimeout: 3000,
          })
          this.$f7.dialog.close()
        })
    },
    isGameFinished(game) {
      const p1Wins = game[0] - game[1] >= 2 && game[0] >= this.match.pointsPerSet
      const p2Wins = game[1] - game[0] >= 2 && game[1] >= this.match.pointsPerSet
      return p1Wins || p2Wins
    },
    broadcastCurrentResult() {
      const totalPoints =
        this.match.resultTable[this.currentDisplayingSet][0] +
        this.match.resultTable[this.currentDisplayingSet][1]
      const winnerOfSet =
        this.match.resultTable[this.currentDisplayingSet][0] >
        this.match.resultTable[this.currentDisplayingSet][1]
          ? 0
          : 1
      const textEnding = this.isGameFinished(this.match.resultTable[this.currentDisplayingSet])
        ? `${this.match[`player${winnerOfSet + 1}`].fullname} won`
        : `${this.match[`player${this.currentServer + 1}`].fullname} is serving`
      const text =
        totalPoints % 2 === 0
          ? `${this.match.resultTable[this.currentDisplayingSet][this.currentReceiver]} : ${
              this.match.resultTable[this.currentDisplayingSet][this.currentServer]
            }, ${this.match.resultTable[this.currentDisplayingSet][this.currentServer]} : ${
              this.match.resultTable[this.currentDisplayingSet][this.currentReceiver]
            }, ${textEnding}.`
          : `${this.match.resultTable[this.currentDisplayingSet][this.currentServer]} : ${
              this.match.resultTable[this.currentDisplayingSet][this.currentReceiver]
            }, ${textEnding}.`
      const msg = new SpeechSynthesisUtterance(text)
      msg.voice = window.speechSynthesis.getVoices()[49]
      if (window.speechSynthesis.speaking) {
        window.speechSynthesis.cancel()
      }
      window.speechSynthesis.speak(msg)
    },
    win(playerIdx) {
      if (!this.canMakeChange) return

      // is match finished?
      if (this.isMatchFinished) {
        this.$f7.toast.show({
          text: 'Match is finished.',
          closeTimeout: 3000,
        })
        return
      }
      // is previous game finished?
      if (
        this.currentDisplayingSet > 0 &&
        !this.isGameFinished(this.match.resultTable[this.currentDisplayingSet - 1])
      ) {
        this.$f7.toast.show({
          text: 'Please finish the previous game.',
          closeTimeout: 3000,
        })
        return
      }
      const game = this.match.resultTable[this.currentDisplayingSet]
      // is game finished?
      if (this.isGameFinished(game)) {
        this.$f7.toast.show({
          text: 'Game is finished.',
          closeTimeout: 3000,
        })
        return
      }
      this.$db
        .ref(`matches/${this.match.id}/resultTable/${this.currentDisplayingSet}/${playerIdx}`)
        .set(this.match.resultTable[this.currentDisplayingSet][playerIdx] + 1)
        .then(() => {
          this.broadcastCurrentResult()
        })
        .catch((error) => {
          this.$f7.toast.show({
            text: error.message,
            closeTimeout: 3000,
          })
        })
    },
    decrease(playerIdx) {
      if (!this.canMakeChange) return

      // is match saved?
      if (this.isMatchSaved) {
        this.$f7.toast.show({
          text: 'Match is already saved.',
          closeTimeout: 3000,
        })
        return
      }
      const game = this.match.resultTable[this.currentDisplayingSet]
      if (game[playerIdx] === 0) return
      const otherPlayerIdx = playerIdx === 0 ? 1 : 0
      const isOtherPlayerWinning =
        game[otherPlayerIdx] > game[playerIdx] && this.isGameFinished(game)
      if (isOtherPlayerWinning) return
      this.$db
        .ref(`matches/${this.match.id}/resultTable/${this.currentDisplayingSet}/${playerIdx}`)
        .set(this.match.resultTable[this.currentDisplayingSet][playerIdx] - 1)
        .catch((error) => {
          this.$f7.toast.show({
            text: error.message,
            closeTimeout: 3000,
          })
        })
    },
    save() {
      if (!this.canMakeChange) return

      this.$f7.dialog.confirm('Do you want to save match now?', 'Save Match', this.confirmSave)
    },
    confirmSave() {
      if (!this.canMakeChange) return

      this.$f7.dialog.preloader('Saving match...')
      this.$fns
        .httpsCallable('saveMatch')({
          matchId: this.match.id,
        })
        .then(() => {
          this.$f7.dialog.close()
          this.$f7.toast.show({
            text: 'Match saved.',
            closeTimeout: 3000,
          })
        })
        .catch((error) => {
          this.$f7.dialog.close()
          this.$f7.toast.show({
            text: error.message,
            closeTimeout: 3000,
          })
        })
    },
    cancel() {
      if (!this.canMakeChange) return

      this.$f7.dialog.confirm(
        'Do you want to cancel this match? Note: when a game is canceled, it will be removed completely.',
        'Cancel Match',
        this.confirmCancel,
      )
    },
    confirmCancel() {
      if (!this.canMakeChange) return

      this.$f7.dialog.preloader('Canceling match...')
      this.$fns
        .httpsCallable('cancelMatch')({
          matchId: this.match.id,
        })
        .then(() => {
          this.$f7.dialog.close()
          this.$f7.toast.show({
            text: 'Match canceled.',
            closeTimeout: 3000,
          })
        })
        .catch((error) => {
          this.$f7.dialog.close()
          this.$f7.toast.show({
            text: error.message,
            closeTimeout: 3000,
          })
        })
    },
    leave() {
      this.$f7.dialog.confirm(
        'Do you want to leave this match and go back to matches page?',
        'Leave Match',
        () => {
          this.$f7router.navigate('/matches', { reloadCurrent: true })
        },
      )
    },
  },
}
</script>

